.projectOuter{height:232px;margin-top:1rem;padding:0;width:33%}.project{border:0 solid #7d97ad;border-radius:2px;box-shadow:0 1px 1px 0 rgba(0,0,0,.14),0 1px 3px 0 rgba(0,0,0,.12),0 2px 1px -2px rgba(0,0,0,.2);display:flex;flex-direction:column;height:232px;margin:0 .4rem;padding:16px 1rem;transition:box-shadow .25s}.project:hover{transition-delay:.2s}.projectTitle,.projectTitleSmall{display:flex;flex-direction:row;height:auto;min-height:2rem}.projectTitle{font-size:1.5rem}.projectTitleSmall{font-size:1rem}.projectName{margin-right:1rem}.projectDate{display:inline-block;font-size:.8rem;margin-top:8px;opacity:.85}.projectDesc{flex-grow:100;font-size:.85rem;margin-top:.5rem;opacity:.65}.projectDesc:hover{cursor:pointer;opacity:.8}.projectTags{margin-bottom:.5rem;margin-top:1rem}.projectLinks{margin-left:1rem}@media (max-width:1200px){.project{margin:0 .35rem}}@media (max-width:1155px){.projectTitle{font-size:1.3rem}.projectTitleSmall{font-size:1.1rem}}@media (max-width:1000px){.projectOuter{width:50%}.project{margin:0 .5rem}}@media (max-width:820px){.projectOuter{height:242px}.project{height:210px}.projectTitle,.projectTitleSmall{min-height:unset}}@media (max-width:600px){.projectOuter{height:282px}.project{height:250px;margin:0 .35rem}}@media (max-width:550px){.projectOuter{height:222px;width:100%}.project{height:190px;margin:0 auto;max-width:85%}}@media (max-width:400px){.project{max-width:90%}}@media (max-width:350px){.projectOuter{height:232px}.project{height:200px}}